            // bottom right:
            context.arc(
                this.width() - this.corner - edge,
                y,
                this.corner,
                radians(0),
                radians(90),
                false
            );

            // bottom left:
            context.arc(
                this.corner + edge,
                y,
                this.corner,
                radians(90),
                radians(180),
                false
            );

