        // CostumeEditorMorph ////////////////////////////////////////////////////////

        // CostumeEditorMorph inherits from Morph:

        CostumeEditorMorph.prototype = new Morph();
        CostumeEditorMorph.prototype.constructor = CostumeEditorMorph;
        CostumeEditorMorph.uber = Morph.prototype;

